.slideshow__list {
    list-style: none;

    & li {
        width: 80px;
        height: 80px;
        border: 1px solid #eee;
        margin-bottom: 8px;
        cursor: pointer;

        & img {
            object-fit: cover;
        }
    }
}

.slideshow__preview {
    border: 1px solid #eee;
}