.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 200;
  background-color: rgba(0, 0, 0, 0.35);
}

.popup-container {
  position: fixed;
  background-color: #ffffff;
  z-index: 1020;
  top: 15vh;
  left: 50%;
  transform: translate(-50%, 0);
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 6px;
  overflow: hidden;
}

.popup-close {
  position: absolute;
  right: 20px;
  z-index: 1030;
  top: 10px;
  font-size: 28px;
  font-weight: bold;
  color: #999;
  cursor: pointer;

  &:hover {
    color: #333;
  }
}